import { CLASS_NAMES, TYPES } from "../constants"
import { HighlightItem } from "./generate-rpjson-stylesheet"

type Props = {
  sentenceNumber?: number
  paragraphNumber?: number
}

export const highlightSentence = (
  style: object = {},
  { sentenceNumber = 0, paragraphNumber = 0 }: Props = {},
): HighlightItem => {
  return {
    style,
    className: `& .${CLASS_NAMES.TYPE_PREFIX}${TYPES.SENTENCE}.${CLASS_NAMES.ID_PREFIX}${CLASS_NAMES.SENTENCE_NUMBER_PREFIX}${paragraphNumber}-${sentenceNumber}`,
  }
}

export const highlightAllSentences = (style: object = {}): HighlightItem => {
  return {
    style,
    className: `& .${CLASS_NAMES.TYPE_PREFIX}${TYPES.SENTENCE}`,
  }
}
